ent />\r\n\r\n            <template v-slot:append>\r\n                <v-divider></v-divider>\r\n                <v-card-text class=\"text-center py-1\">\r\n                    <v-btn class=\"mx-4\" icon link @click=\"salir\">\r\n                        <v-icon size=\"24px\" color=\"blue-grey lighten-4\">\r\n                            mdi-power\r\n                        </v-icon>\r\n                    </v-btn>\r\n                </v-card-text>\r\n            </template>\r\n        </v-navigation-drawer>\r\n        <v-app-bar app color=\"white\" scroll-off-screen elevation=\"3\">\r\n            <v-app-bar-nav-icon\r\n                @click.stop=\"drawer = !drawer\"\r\n            ></v-app-bar-nav-icon>\r\n\r\n            <v-spacer />\r\n\r\n            <v-menu offset-y>\r\n                <template v-slot:activator=\"{ attrs, on }\">\r\n                    <v-btn\r\n                        text\r\n                        color=\"primary\"\r\n                        class=\"\"\r\n                        v-bind=\"attrs\"\r\n                        v-on=\"on\"\r\n                    >\r\n                        {{ user.nombres }}\r\n                        <v-icon right>mdi-account </v-icon>\r\n                    </v-btn>\r\n                </template>\r\n\r\n                <v-list dense>\r\n                    <v-subheader>Opciones</v-subheader>\r\n                    <v-list-item-group v-model=\"selectedMenu\" color=\"primary\">\r\n                        <v-list-item\r\n                            v-for=\"(item, i) in items\"\r\n                            :key=\"i\"\r\n                            @click=\"SelectMenu(item.text)\"\r\n                        >\r\n                            <v-list-item-icon>\r\n                                <v-icon v-text=\"item.icon\"></v-icon>\r\n                            </v-list-item-icon>\r\n                            <v-list-item-content>\r\n                                <v-list-item-title\r\n                                    v-text=\"item.text\"\r\n                                ></v-list-item-title>\r\n                            </v-list-item-content>\r\n                        </v-list-item>\r\n                    </v-list-item-group>\r\n                </v-list>\r\n            </v-menu>\r\n        </v-app-bar>\r\n\r\n        <v-main class=\"main_app\">\r\n            <transition name=\"component-fade\" mode=\"out-in\">\r\n                <slot />\r\n            </transition>\r\n        </v-main>\r\n        <v-footer app dark>\r\n            <v-spacer />\r\n            <h6>\r\n                <!--by\r\n                <a\r\n                    href=\"https://www.linkedin.com/in/lino-puma-ticona-a76021202/\"\r\n                    target=\"_blank\"\r\n                    class=\"blue--text text-decoration-none\"\r\n                    >Lino Puma</a\r\n                > -->\r\n            </h6>\r\n        </v-footer>\r\n    </v-app>\r\n</template>\r\n<script>\r\nimport MenuPrincipalComponent from \"@/components/MenuPrincipalComponent\";\r\nexport default {\r\n    components: {\r\n        MenuPrincipalComponent,\r\n    },\r\n    data: () => ({\r\n        drawer: true,\r\n        attrs: {\r\n            class: \"mb-1\",\r\n            boilerplate: true,\r\n            elevation: 0,\r\n            loading: false,\r\n        },\r\n\r\n        closeOnContentClick: true,\r\n\r\n        items: [\r\n            { text: \"Perfil\", icon: \"mdi-account\" },\r\n            { text: \"Salir\", icon: \"mdi-power\" },\r\n        ],\r\n        selectedMenu: null,\r\n    }),\r\n    computed: {\r\n        user() {\r\n            return this.$page.props.auth.user;\r\n        },\r\n    },\r\n    methods: {\r\n        SelectMenu(op){\r\n            if(op == 'Salir'){\r\n                this.salir();\r\n            }\r\n        },\r\n        salir() {\r\n            this.$inertia.post(\"/logout\");\r\n        },\r\n    },\r\n    async created() {\r\n        //await this.GetCurrentUser();\r\n\r\n        this.drawer = this.$vuetify.breakpoint.width > 960 ? true : false;\r\n    },\r\n};\r\n</script>\r\n<style scoped>\r\n@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&display=swap');\r\n* {\r\n    font-family: 'Nunito', sans-serif;\r\n}\r\n.main_app {\r\n    background: #eeeeee;\r\n}\r\n.component-fade-enter-active,\r\n.component-fade-leave-active {\r\n    transition: opacity 0.3s ease;\r\n}\r\n.component-fade-enter, .component-fade-leave-to\r\n/* .component-fade-leave-active below version 2.1.8 */ {\r\n    opacity: 0;\r\n}\r\n.wrap-imagen {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    width: 100%;\r\n    background-color: rgba(255,255,255,.3);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
