uardar()\"\r\n                                        >\r\n                                            {{\r\n                                                is_nuevo\r\n                                                    ? \" Registrar\"\r\n                                                    : \"Guardar Cambios\"\r\n                                            }}\r\n                                        </v-btn>\r\n                                    </div>\r\n                                </v-form>\r\n                            </v-card>\r\n                        </v-tab-item>\r\n\r\n                        <v-tab-item>\r\n                            <v-card class=\"py-4\" flat tile>\r\n                                <v-card-title>\r\n                                    Areas Asignadas\r\n\r\n                                    <v-spacer></v-spacer>\r\n                                    <v-btn\r\n                                        color=\"secondary\"\r\n                                        @click=\"\r\n                                            dialog_asignar = !dialog_asignar\r\n                                        \"\r\n                                    >\r\n                                        Asignar Areas\r\n                                    </v-btn>\r\n                                </v-card-title>\r\n                                <v-expansion-panels focusable accordion tile>\r\n                                    <v-expansion-panel\r\n                                        v-for=\"(item, i) in form.oficinas\"\r\n                                        :key=\"i\"\r\n                                    >\r\n                                        <v-expansion-panel-header>\r\n                                            <strong class=\"primary--text\">{{\r\n                                                item.nombre\r\n                                            }}</strong>\r\n                                        </v-expansion-panel-header>\r\n\r\n                                        <v-expansion-panel-content>\r\n                                            <template v-if=\"areas_listas\">\r\n                                                <v-list subheader two-line>\r\n                                                    <v-subheader inset>\r\n                                                        Areas asignadas a\r\n                                                        {{ form.nombres }}\r\n                                                    </v-subheader>\r\n\r\n                                                    <v-list-item\r\n                                                        v-for=\"(\r\n                                                            area, index\r\n                                                        ) in item.areas\"\r\n                                                        :key=\"index\"\r\n                                                    >\r\n                                                        <v-list-item-avatar>\r\n                                                            <v-icon\r\n                                                                class=\"warning\"\r\n                                                                dark\r\n                                                            >\r\n                                                                mdi-office-building-marker\r\n                                                            </v-icon>\r\n                                                        </v-list-item-avatar>\r\n\r\n                                                        <v-list-item-content>\r\n                                                            <v-list-item-title\r\n                                                                v-text=\"\r\n                                                                    area.nombre\r\n                                                                \"\r\n        